import { Component } from '@angular/core';
import { NavController,ToastController } from 'ionic-angular';
import {Login} from '../../providers/login';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  login: String = null;
  mdp: String = null;
  loginTest : String = null;
  mdpTest : String = null;
  constructor(public navCtrl: NavController,private signinService: Login,private toastController: ToastController) {
  }

  onSubmit() {
    this.signinService.login({"username":this.login,"password":this.mdp}).subscribe(
      token => {
        
        this.signinService.token = token.token;
        let toast = this.toastController.create({
          message: 'Authentification succes!',
          duration: 2000
        });
        toast.present();
        
      }
      ,()=> {
        let toast = this.toastController.create({
          message: 'Authentification EchouÃ©e!',
          duration: 2000
        });
        toast.present();
    });
  } 
 

}



